import React from "react";
import TopArea from "./TopArea";
import FooterArea from "./FooterArea";
import MainArea from "./MainArea";

import { dCon } from "../modules/dCon";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";

//  css
import "../../css/common/reset.scss";
import "../../css/common/common.scss";
import "../../css/common/core.scss";
import "../../css/common/grid_12_flex.scss";
import Loding from "./Loding";





export default function Layout() {
   // [   ] 
    // 1.  
    const [loginSts,setLoginSts] = 
    useState(sessionStorage.getItem("minfo"));
    // useState("");
    // ->   "minfo" 

    // 2.    
    const [loginMsg, setLoginMsg] = useState(null);
    // console.log(loginMsg);

    // [   ]
    // 1.  
    const goPage = useNavigate();
    // 2.    
    const makeMsg = (name) => {
        // 
        let usrIcon = ["","","","",""];
        // : 0~4 
        let rdm = Math.floor(Math.random() * 5);
        //    
        setLoginMsg(` Welcome ${name}  `);
    }; //// makeMsg  /////

    // 3.  
    const logoutFn = () => {
        // 1.   null 
        setLoginSts(null);
        // 2.   : minfo
        sessionStorage.removeItem("minfo");
        // 3.   
        setLoginMsg(null);
        // 4.  
        goPage("/");

    }; //// logoutFn  ////

    //   
    useEffect(()=>{
        // ->   
        //  (minfo)  null   
        //    .
        // -> null   true
        if(sessionStorage.getItem("minfo")){
            //   
            let ss =sessionStorage.getItem("minfo");
            //  
            setLoginSts(ss);
            //    :  unm() 
            makeMsg(JSON.parse(ss).unm);
        }//// if
    },[]);


  return (
    <dCon.Provider value={{
      loginSts,
      setLoginSts,
      loginMsg,
      setLoginMsg,
      goPage,
      makeMsg,
      logoutFn
      }}>
        <Loding />
      <TopArea />
      <MainArea />
      <FooterArea />
    </dCon.Provider>
  );
}

