import mFn from "../my_function.js";

const addEvt = (ele, evt, fn) => ele.addEventListener(evt, fn);
// HTML  loadFn ! ///

export default function Sub1SlideFn() {
  // console.log("!");
  //    : .abtn
  const abtn = mFn.qsa(".s1abtn");
  //   : #slide
  const slide = mFn.qs(".S1-slide");
  //console.log(abtn,slide);
  const chslide = mFn.qs(".S1-slide");
  console.log(chslide);

  //  : .indic
  let indic = document.querySelector(".S1indic");
  ////  
  if(chslide){
    for (let i = 0; i < 18; i++) {
      //  
      chslide.innerHTML += `
      <li data-seq="${i}">
        <img src=${process.env.PUBLIC_URL}/images/img1/ched${i+1}.jpg alt="slide">
      </li>
      `;
  
      //  
      indic.innerHTML += `
      <li ${i===0?'class="on"':''}>
      <img src=${process.env.PUBLIC_URL}/images/img1/dot1.png alt="">
      <img src=${process.env.PUBLIC_URL}/images/img1/dot2.png alt="">
      </li>
      `;
    } ////////// for ///////
  }

  //  li ! indic 
  indic = document.querySelectorAll('.S1indic li');

  // 2.    
  for (let x of abtn) {
    console.log(x);
    x.onclick = function(e){
      e.preventDefault();
      goSlide(e);
    };
  } //////////////// for

  //   
  let prot = false;
  /***************************************************************************************************************
                                           : goSlide
                                           :  
  ***************************************************************************************************************/

  function goSlide(evt,sts=true){

    
    console.log(':',evt,sts,evt.currentTarget);
    
    //        
    if(sts){
      clearAuto();
    } //////// if ///////////

    if (prot) return;
    prot = true;
    setTimeout(() => {
      prot = false;
    }, 600);
    /////////////////////////////////////////////

    //   .ab2?
    let isRbtn =
    sts?evt.currentTarget.classList.contains("ab2"):true;

    //   
    console.log(" ",evt.currentTarget.classList.contains("ab2"), isRbtn);


    ///// 2.   ()
    //    //
    if (isRbtn) {
      // (1)   
      slide.style.left = "-100%";
      slide.style.transition = ".6s ease-in-out";

      // (2)   0.6 
      setTimeout(() => {
        //  li  
        slide.appendChild(slide.querySelectorAll("li")[0]);
        //  left  -100% left 0 
        slide.style.left = "0";
        // left  
        slide.style.transition = "none";
      }, 600);

    } // if ///

    ///   
    else {
      //  li 
      let list = slide.querySelectorAll("li");

      // (1)  li  
      slide.insertBefore(list[list.length - 1], list[0]);

      // (2) left  -100% 
      slide.style.left = "-100%";

      slide.style.transition = "none";

      setTimeout(() => {
        /// (3) left  0  
        slide.style.left = "0";
        slide.style.transition = ".6s ease-in-out";
      }, 0);
    } ////// else

    // 3.      

    let seq = slide.querySelectorAll('li')[isRbtn?1:0].getAttribute('data-seq');
console.log(":",seq);


    // 4.  
    indic.forEach((ele,idx)=>{

      // ele -  li , idx -  
     if(idx==seq){
       ele.classList.add('on');
     } /// if ////
     else {
       ele.classList.remove('on');
     } //// else ////
   }); /// forEach ///
  } /////////////////// goSlide 

////////////////   ()
let autoI;
//   ( )
let autoT;
//    
autoSlide();

////////////////     /////////////////////////////////
function autoSlide(){
  autoI = setInterval(() => {
  }, 3000);
} ///////// autoSlide 


////////////    /////////////////
function clearAuto(){

   //console.log(' ');

   clearInterval(autoI);

   clearTimeout(autoT);

   autoT = setTimeout(() => {
     autoSlide();
   }, 5000);
} ///////// clearAuto

} //////////////// sub1SlideFn  ///////////////