  // 1.  ////////////
  let stopSts = false;
  let pgNum = 0; // 1-1. 
  const setPgNum = (x) => pgNum = x;
  let stsWheel = false; // 1-2.   (true  / false )
  let elePage,totalCnt;
  window.addEventListener("DOMContentLoaded", () => {
    elePage = mFn.qsa(".section"); // 1-3. .section 
    totalCnt = 8;//elePage.length; // 1-4. 
    console.log(":",totalCnt);
  });
  // console.log(":", elePage, totalCnt, "");

  //   wheelFn 
  // mFn.addEvt(window, "wheel", wheelFn, { passive: false });

  // [    ]http://127.0.0.1:5501/%ED%8C%80%ED%94%8C/test/sub2.html###
  setTimeout(() => {
    window.scroll(0, 0);
  }, 500);

  // const contBox = mFn.qsa(".wh-slide");
  let contBox;
  window.addEventListener("DOMContentLoaded",()=>{
    contBox = mFn.qsa(".content-box");
  })
  // console.log(contBox);

  // contBox.forEach((ele) => {
  //   mFn.addEvt(ele, "wheel", setContBoxWheelEvent);
  // });

  function setContBoxWheelEvent(e) {
    
    if (stopSts) e.stopPropagation();

    const scrollPercentage = Math.floor(getScrollPercentage(e.currentTarget));

    console.log(scrollPercentage + "%");
    console.log(e.deltaY);
    if (pgNum === 6 || pgNum === 5) {
      // 0 
      if (scrollPercentage < 5 && e.deltaY < 0) {
        stopSts = false;
      }
      // 100 
      else if (scrollPercentage > 95 && e.deltaY > 0) {
        stopSts = false;
      }
      //  
      else {
        stopSts = true;
      }
    }
    console.log("~~~~!",stopSts);
  } /////// setContBoxWheelEvent ///////////////////

  // // 3.   ////////////////
function wheelFn(e) {
  e.preventDefault();
    // setTimeout    
    // content-box    !!!!!!!!!!!!!!!!!!!!!!!!!!!!
    // stopSts = false;
    // setTimeout(() => {
    //   stopSts = true;
    // }, 500);

    console.log(":", stopSts);

    if (stopSts) return;

    const contBox = mFn.qsa(".wh-slide");

    // 2.  ////////
    if (stsWheel) {
      return; //
    }
    stsWheel = true; //!!
    setTimeout(() => {
      stsWheel = false;
    }, 500);
    //////////////////////////

    //  3.   ///
    let delta = e.wheelDelta;
    //    
    // ,  .
    // console.log(":", delta);
    //  ->  !

    // 4.   ////
    if (delta < 0) {
      //   
      pgNum++;
      //   ()
      if (pgNum === totalCnt) {
        pgNum = totalCnt - 1;
        //
        //  -1
      } //////if ///////
    } ///// if /////

    // else if(pgNum){}
    else {
      pgNum--;
      //  (0  0)
      if (pgNum < 0) {
        pgNum = 0;
      } ///// if ////////
    } ///// else //////
    console.log(":", pgNum);

    // 5.   ///
    // 5-1.  
    // -> .page   
    let pos = $(".section").eq(pgNum).offset().top;
    //  offsetTop   
    // console.log(":", pos);

    // 5-2.    
    // scrollTo(0,y)
    // window.scrollTo(0,pos);
    $("html,body").animate({scrollTop: pos+"px"},600);

    console.log("pgNum:",pgNum);
    ////////////////////////////////
    //      0 
    ////////////////////////////////
    if (pgNum != 5 || pgNum != 6) {
      console.log("???");
      contBox.forEach((ele) => {
        ele.scrollTo(0, 0);
      });
      // mySmooth1.setScrollPos(0);
      // mySmooth2.setScrollPos(0);
    }
    ////////////////////////////////
    ////////////////////////////////

    // 6.  on / on
    chgMenu(pgNum);
  } /////////// wheelFn  ////////////////
  ///////////////////////////////////////////
  /**********************************
       
    ********************************* */
  //    : .gnb a
  const gnb = document.querySelector(".gnb");
  const gnbA = document.querySelectorAll(".gnb a");
  //   + 
  gnbA.forEach((ele, idx) => {
    const contBox = mFn.qsa(".cont-box");
    ele.onclick = () => {
      //   
      chgMenu(idx);
      let txt = ele.innerText;
      console.log(ele.innerText);
      if (txt !== "AWARDS" || txt !== "CREDITS") {
        contBox.forEach((ele) => {
          ele.scrollTo(0, 0);
        });
      }
    }; //////click ////
  }); //////// forEach ////////////////

  // [  : .gnb + .indic]
  function chgMenu(idx) {
    // idx - 
    //   
    console.log(":", idx);
    console.log(":", gnbA);

    // 1.  
    setPgNum(idx);
    // 2. on
    gnbA.forEach((ele, seq) => {
      // ele - a / seq - 
      if (idx === seq) {
        //  on
        ele.parentElement.classList.add("on");
      } /////if //////
      else {
        ele.parentElement.classList.remove("on");
      } ////// else///////
    }); //////forEach/////////
  } ///////////chgMenu ///////////
  ///////////////////////////////////

  function getScrollPercentage(element) {
    console.log(element);
    const scrollTop = element.scrollTop;
    const scrollHeight = element.scrollHeight;
    const clientHeight = element.clientHeight;

    const scrollableHeight = scrollHeight - clientHeight;
    const scrollPercentage = (scrollTop / scrollableHeight) * 100;

    return scrollPercentage;
  }

  export {wheelFn, setContBoxWheelEvent, setPgNum,chgMenu};