//    - Member.jsx
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

//   JS
import { initData } from "../func/mem_fn";

//  CSS 
import "../../css/member.scss";

function Member() {
    //   
    const goNav = useNavigate();
    

    // [  ] /////////////
    // [1]  
    // 1. 
    const [userId, setUserId] = useState("");
    // 2. 
    const [pwd, setPwd] = useState("");
    // 3. 
    const [chkPwd, setChkPwd] = useState("");
    // 4. 
    const [userName, setUserName] = useState("");
    // 5. 
    const [email, setEmail] = useState("");

    // [2]  
    // ->   (false)
    // 1. 
    const [userIdError, setUserIdError] = useState(false);
    // 2. 
    const [pwdError, setPwdError] = useState(false);
    // 3. 
    const [chkPwdError, setChkPwdError] = useState(false);
    // 4. 
    const [userNameError, setUserNameError] = useState(false);
    // 5. 
    const [emailError, setEmailError] = useState(false);

    // console.log(userIdError);

    // [    ] ////
    const msgId = [
        // 1.  5  
        "User ID must contain a minimum of 5 characters",
        // 2.   
        "This ID is already in use!",
        // 3.  !
        "That's a great ID!",
    ];

    // [    ]
    const msgEtc = {
        // 
        pwd: "5 to 15 digits in the form of special characters, characters, and numbers",
        // 
        confPwd: "Password verification does not match",
        // 
        req: "This is a required entry",
        // 
        email: "Please enter a valid email format",
    }; ///// msgEtc ///////

    // [3]   :  msgId[0]
    // ->   
    const [idMsg, setIdMsg] = useState(msgId[0]);

    // [    ] ///////
    // 1.    ////////////
    const changeUserId = (e) => {
        //  
        let val = e.target.value;

        // 1.   (  !)
        const valid = /^[A-Za-z0-9+]{5,}$/;
        //  : .test()

        // 2.   : e.target
        // console.log(val);

        // 3.  
        // 3-1.   (  )
        if (valid.test(val)) {
            console.log("...!");
            //      !
            initData();
            //  "mem-data"  !

            //     !!!
            // 1.  
            let memData = localStorage.getItem("mem-data");
            console.log(memData);

            // 2.   (? !)
            memData = JSON.parse(memData);
            console.log(memData);
            // ->  !
            // : JSON    
            //    (  )

            // 3.    
            //    !
            //  true,  false
            let isT = memData.some((v) => v.uid === val);
            console.log("id?", isT);

            // 4. true     
            if (isT) {
                //   
                setIdMsg(msgId[1]);
                //  
                setUserIdError(true);
            } ///// if /////
            // 5. false   [ ] 
            else {
                //   :  !(false)
                setUserIdError(false);
            } ///// else //////

            //   (false)
            //   setUserIdError(false);
        } /// if /////////////////////////
        // 3-2.  :   
        else {
            console.log("~!");
            //   
            setIdMsg(msgId[0]);
            //   (true)
            setUserIdError(true);
        } /// else ///

        //  userId   
        //  !
        setUserId(val);
    }; ////////// changeUserId  ////////////

    // 2.    ///////////
    const changePwd = (e) => {
        //  
        let val = e.target.value;

        // 1.   (  !)
        const valid = /^.*(?=^.{5,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;

        // 2.   : e.target ->   
        // console.log(val);

        // 3.    
        if (valid.test(val)) setPwdError(false);
        else setPwdError(true);

        // 4.  
        setPwd(val);
    }; ///////// changePwd  //////////

    // 3.    ///////////
    const changeChkPwd = (e) => {
        //  
        let val = e.target.value;

        // 1.    
        if (pwd === val) setChkPwdError(false);
        else setChkPwdError(true);

        // 2.  
        setChkPwd(val);
    }; ///////// changeChkPwd  //////////

    // 4.    ///////////
    const changeUserName = (e) => {
        //  
        let val = e.target.value;

        // 1. 
        if (val !== "") setUserNameError(false);
        else setUserNameError(true);

        // 2.  
        setUserName(val);
    }; ///////// changeUserName  //////////

    // 5.    ///////////
    const changeEmail = (e) => {
        //  
        let val = e.target.value;

        // 1.   (  !)
        const valid = /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;

        // 2.   : e.target ->   
        // console.log(val);

        // 3.    
        if (valid.test(val)) setEmailError(false);
        else setEmailError(true);

        // 4.  
        setEmail(val);
    }; ///////// changeEmail  //////////

    // [    ] ///////////
    const totalValid = () => {
        // 1.     !
        if (!userId) setUserIdError(true);
        if (!pwd) setPwdError(true);
        if (!chkPwd) setChkPwdError(true);
        if (!userName) setUserNameError(true);
        if (!email) setEmailError(true);

        // 2.  true,  false 
        //  :  +   false
        if (userId && pwd && chkPwd && userName && email && !userIdError && !pwdError && !chkPwdError && !userNameError && !emailError) return true;
        //  false false !
        else return false;
    }; /////////// totalValid  ///////////

    // [    ]
    const onSubmit = (e) => {
        //   
        e.preventDefault();

        console.log(":", totalValid());

        // 2.   
        if (totalValid()) {
            console.log("! !");

            // [  ]

            // 1.   ( )
            initData();

            // 2.  
            let memData = localStorage.getItem("mem-data");

            // 3.  
            memData = JSON.parse(memData);

            //     (idx)
            let temp = memData.map((v) => v.idx);
            //     +1 
            console.log(",:", Math.max(...temp) + 1);

            // 4.   
            let newData = {
                idx: Math.max(...temp) + 1,
                uid: userId,
                pwd: pwd,
                unm: userName,
                eml: email,
            };

            // 5.   :    push
            memData.push(newData);

            // 6.   :  !
            localStorage.setItem("mem-data", JSON.stringify(memData));

            // 7.   +   
            //   
            document.querySelector(".sbtn").innerText =
            "Thank you for joining us";
            // 1   :  Navigate 
            setTimeout(()=>{
                goNav("/login");
            },1000);

        } /////// if /////
        // 3.  ///
        else {
            alert("change your input! ");
        } /// else
    }; //////// onSubmit  ////

    //  
    const arr = [{ idx: "100" }, { idx: "77" }, { idx: "3" }, { idx: "44" }, { idx: "5" }];
    const newArr = arr.map((v) => v.idx);
    // ... ->    .
    const maxValue = Math.max(...newArr);
    const minValue = Math.min(...newArr);
    // const maxValue = Math.max("77","55","33");
    // console.log(newArr);
    // console.log("", maxValue);
    // console.log("", minValue);

    //   //////////////////
    return (
        <div className="outbx">
            <section className="membx">
                <h2>Join Us</h2>
                <form action="process.php" method="post">
                    <ul>
                        <li>
                            {/* 1.  */}
                            <label>ID : </label>
                            <input type="text" maxLength="20" placeholder="Please enter your ID" value={userId} onChange={changeUserId} />
                            {
                                //      
                                //  && 

                                userIdError && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "red",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {idMsg}
                                        </small>
                                    </div>
                                )
                            }
                            {
                                //   
                                //  && 
                                //  : userId  
                                // userId  false !
                                !userIdError && userId && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "green",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {msgId[2]}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            <label>Password : </label>
                            <input type="password" maxLength="20" placeholder="Please enter your Password" value={pwd} onChange={changePwd} />
                            {
                                //    
                                //  && 

                                pwdError && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "red",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {msgEtc.pwd}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            <label>Confirm Password : </label>
                            <input type="password" maxLength="20" placeholder="Please enter your Confirm Password" value={chkPwd} onChange={changeChkPwd} />
                            {
                                //    
                                //  && 

                                chkPwdError && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "red",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {msgEtc.confPwd}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            <label>User Name : </label>
                            <input type="text" maxLength="20" placeholder="Please enter your Name" value={userName} onChange={changeUserName} />
                            {
                                //    
                                //  && 

                                userNameError && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "red",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {msgEtc.req}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li>
                            <label>Email : </label>
                            <input type="text" maxLength="50" placeholder="Please enter your Email" value={email} onChange={changeEmail} />
                            {
                                //    
                                //  && 

                                // email  false !
                                emailError && (
                                    <div className="msg">
                                        <small
                                            style={{
                                                color: "red",
                                                fontSize: "10px",
                                            }}
                                        >
                                            {msgEtc.email}
                                        </small>
                                    </div>
                                )
                            }
                        </li>
                        <li style={{ overflow: "hidden" }}>
                            <button className="sbtn" onClick={onSubmit}>
                                Submit
                            </button>
                        </li>
                        <li>
                            Are you already a Member?
                            <Link to="/login">Log In</Link>
                        </li>
                    </ul>
                </form>
            </section>
        </div>
    );
}

export default Member;
