import mFn from "../my_function.js";
import { charTextItems } from "./sub2_data.js";

/////////////////////////////////////////////////////////////////////////////////////
export default function showChar() {
  const charItems = [
    // "DESIGN",
    "Woody",
    "Buzz Lightyear",
    "Jessie",
    "Bullseye",
    "Mrs. Potato Head",
    "Weezy",
    "The Prospector",
    "Emperor Zurg",
    "Al",
  ];
  /////////   /////////
  function innerChar1() {
    let hcode = " <ul>";
    const menu = mFn.qsa(".menu")[0];
    charItems.forEach((i) => {
      hcode += `
              <li>
              <a href="###">${i}</a>
              </li>
          `;
    });
    hcode += "</ul>";
    menu.innerHTML = hcode;
  } /////////////////// ///////////////////////////
  innerChar1();

  ///////   /////////////
  function innerChar2() {
    let hcode = "";
    const mainCont = mFn.qsa(".main-cont")[0];
    charTextItems.forEach((text, i) => {
      hcode += `
            <div class=" ab-box ab-box1 char${i + 1}">
              <div class="char-info-box info-box">
               <div class="char-img">
                <img src="${process.env.PUBLIC_URL}/images/img2/char/char_${i + 1}.png" alt="${i + 1}" />
                </div>
                <p class="sub-text2 info-text">${text}</p>
              </div>
              <div class="grid-img">
                ${makeCode(i)}
              </div>
            </div>
            `;
    });
    mainCont.innerHTML = hcode;
  } ////////////////  ///////////////////////////////
  innerChar2();
  /////////////    /////////////
  function makeCode(seq, total = 9) {
    let hcode = "";
    for (let i = 0; i < total; i++) {
      hcode += `
          <a href="###">
            <img src="${process.env.PUBLIC_URL}/images/img2/char/char2/${seq + 1}_${i + 1}.jpg" alt="${
        i + 1
      }">
          </a>
        `;
    } /// for ///
    return hcode;
  } ////////////////makeCode/////////////////////

  function showAB() {
    const abBox = mFn.qsa(".ab-box1");
    // console.log('.ab-box1 ',abBox)
    const menuLi = mFn.qsa(".menu1 ul li");
    // console.log('.menu1 ul li ',menuLi)

    abBox[0].classList.add("show");
    menuLi[0].classList.add("show");

    // 1-1.  : ..menu1 ul li () menuLi
    // 1-2.  : .ab-box ( ) abBox
    menuLi.forEach((li, index) => {
      mFn.addEvt(li, "click", () => {
        // 1-1. menuLi 'show'  `
        // li.classList.remove("show");
        //  ???????????????
        menuLi.forEach((menuLi) => {
          menuLi.classList.remove("show");
        }); /////// forEach //////////////

        // 1-2. ab-box 'show'  `
        abBox.forEach((abBox) => {
          abBox.classList.remove("show");
        }); /////// forEach //////////////

        // 2.  'show'  
        abBox[index].classList.add("show");
        menuLi[index].classList.add("show");
      }); /////// addEvt ///////
    });
  } /////////////////showAB///////////////////////////
  showAB();
}

////////////////     //////////
